export class tc_filelist extends LitElement {
  static properties = {files:{}};
  load_data(url){
    fetch("/dav/"+"/"+"?json_mode=1",{  method: 'PROPFIND'}).then(res=>{res.json().then(res=>this.files=res.files)})
  }
  constructor(){
    super()
    var files
    this.load_data("/")
    //fetch("/dav/"+"/"+"?json_mode=1",{  method: 'PROPFIND'}).then(res=>{res.json().then(res=>this.files=res.files)})
  }
  // Render the UI as a function of component stat
  render() {
    if (!this.files){return }
    return html`${this.files.map((file=>
      html`<p >${file.name}</p>`
    ))}`;
  }
}
customElements.define('tc-filelist', tc_filelist);

